import androidx.compose.runtime.CompositionLocalProvider
import androidx.compose.ui.window.Window
import com.playzinkin.composeclock.utils.SystemClock
import org.jetbrains.skiko.wasm.onWasmReady
import kotlin.js.Date

 fun main() {
    onWasmReady {
        Window("Compose clock") {
            val jsSystemClock = object : SystemClock {
                override fun currentTimeMillis(): Long = Date().getTime().toLong()
                override fun getHour(): Int = Date().getHours()
                override fun getMinutes(): Int = Date().getMinutes()
                override fun getSeconds(): Int = Date().getSeconds()
            }
            CompositionLocalProvider(LocalSystemClock provides jsSystemClock) {
                ComposeClock()
            }
        }
    }
}
